@use "base/variables" as v;
@use "base/mixins" as m;

.contenedor-app {
  .imagen,
  html {
    height: 20rem;
    background-image: url(https://www.google.com/imgres?imgurl=https%3A%2F%2Fdevessport.es%2Fblog%2Fwp-content%2Fuploads%2F2021%2F06%2Fcomo-ganar-al-billar-1024x683.jpg&imgrefurl=https%3A%2F%2Fdevessport.es%2Fblog%2Fcomo-ganar-al-billar%2F&tbnid=pF71ferWSKTyUM&vet=12ahUKEwiZhKLhjKz5AhX_rpUCHei_DQYQMygAegUIARDeAQ..i&docid=9ecnCgmgSuCQrM&w=1024&h=683&q=billar&ved=2ahUKEwiZhKLhjKz5AhX_rpUCHei_DQYQMygAegUIARDeAQ);
    background-size: cover;
    background-position: center center;
    @include m.tablet {
      height: auto;
    }
  }

  .app {
    width: 90%;
    margin: auto;
  }
}
nav.nav {
  width: 80%;
  margin: auto;
  padding: 10px 40px;
  background-color: darken($color: v.$bg-2, $amount: 20%);
}
.user {
  position: absolute;
  color: v.$bg-1;
  top: 0;
  left: 0;
}
.barra {
  margin: 0;

  @include m.tablet {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .boton {
    @include m.boton(v.$bg-2, v.$blanco);
    margin: 0;
  }
}

.barra-servicios {
  margin-bottom: v.$separacion;

  @include m.tablet {
    display: flex;
    justify-content: space-between;
  }

  .boton {
    margin: 0 0 0 0;
  }
}
.tala {
  background-color: darken($color: v.$bg-4, $amount: 2%);
}
