@use "base/variables" as v;
@use "base/mixins" as m;

.contenedor-cliente-mesa {
  display: flex;
  margin: auto;
  .datos-mesa {
    display: flex;
    width: 28%;
    text-align: center;
    h4 {
      width: 100%;
      margin: auto;
      text-align: center;
    }
  }
  section.articulos {
    background-color: lighten($color: v.$bg-4, $amount: 10%);
    width: 100%;
    height: 100%;
    position: relative;
    display: flex;
    flex-direction: row-reverse;
    margin: 0.3rem;
    border-radius: 20px;
    padding: 3rem;

    .articulos-list {
      width: 100%;
      display: flex;
      flex-direction: column;
      .articulo-list {
        display: flex;
        margin: 1rem;
        align-items: center;
        justify-content: space-around;

        .articulo {
          margin: auto;
          display: block;
        }
      }
      .campo-articulo {
        color: v.$blanco;
        text-align: center;
        border-radius: 30px;
        margin: auto;
        width: 40%;
        height: 2rem;
        background-color: v.$bg-4;
      }
    }
    .cumulado {
      width: 20%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      p {
        padding: 1rem;
      }
      h4 {
        padding: 1rem;
        text-align: center;
        margin: auto;
      }
    }
  }
}
