@use "base/variables" as v;
@use "base/mixins" as m;

.modal {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.799);
  top: 0;
  right: 0;
  left: 0;
  right: 0;
  bottom: 0;
  .campo {
    width: 90%;
    margin: 10rem auto;
    display: flex;
    flex-direction: column;

    h2 {
      text-align: center;
    }
    .inputs {
      text-align: center;
      margin: auto;
      .submit-nueva-tarea {
        @include m.boton(v.$bg-3, v.$blanco);
      }
      .cerrar-modal {
        @include m.boton(v.$bg-4, v.$blanco);
      }
    }
  }
}
.cerrar {
  display: none;
}
