@use "base/variables" as v;
@use "base/mixins" as m;

section.mesa {
  width: 100%;
  position: relative;
  .id-mesa {
    position: absolute;
  }

  @include m.grid(2, 55px);
  .mesa-contenedor {
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-between;
    background-color: lighten($color: v.$bg-4, $amount: 10%);
    padding: 3px;
    border-radius: 4px;
    border: 4px solid lighten($color: v.$bg-4, $amount: 60%);
    .mesa-listado {
      margin-bottom: 8rem;
      .cliente-contenedor {
        .cliente-acciones {
          display: flex;
          width: 30%;
        }
      }
    }
    p.mesa {
      width: 100%;
      text-align: start;
    }
    .acciones-mesa {
      .ver {
      }
      .boton {
      }
      .boton-eliminar-mesa {
        border-radius: 35px;
        padding: 14px;
        margin: auto auto auto 94%;
      }
    }
  }
  .mesa-billar-contenedor {
    background-color: lighten($color: v.$bg-4, $amount: 40%);
    color: v.$bg-4;
    border-radius: 14px;
  }
}
