@use "base/variables" as v;
@use "base/mixins" as m;

section {
  width: 90%;

  .cliente-contenedor {
    display: flex;
    margin: auto;
    justify-content: space-around;
    align-items: center;

    p,
    a {
      text-decoration: none;
      color: v.$blanco;

      width: 100%;
      text-align: center;
    }
    a {
      width: 10%;
      margin: 1rem;
      padding: 1.2rem;
      border-radius: 3rem;
    }
    a:hover {
      color: v.$bg-4;
      background-color: v.$blanco;
      transition: color;
      transition-delay: 4s;
    }
    .acciones {
      .ver {
      }
      .boton {
      }
      .boton-eliminar {
      }
    }
  }
}
