@use "base/variables" as v;
@use "base/mixins" as m;

.formulario {
  margin-top: 2rem;
  width: 100%;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
  label {
    flex: 0 0 10rem;
    margin: auto 1rem auto 4rem;
  }
  input {
    flex: 1;
    border: none;
    padding: calc(v.$separacion / 4);
    border-radius: 1rem;

    &:disabled {
      background-color: lighten(v.$negro, 30%);
      color: v.$blanco;
      cursor: not-allowed;
    }
  }
}
