  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1e3231;
  color: #ffffff;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1,
h2,
h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #ffffff;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #ffffff;
  font-size: 2rem;
}

.nombre-pagina {
  text-align: center;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
  margin: 1rem;
}

.contenedor-app .imagen,
.contenedor-app html {
  height: 20rem;
  background-image: url(https://www.google.com/imgres?imgurl=https%3A%2F%2Fdevessport.es%2Fblog%2Fwp-content%2Fuploads%2F2021%2F06%2Fcomo-ganar-al-billar-1024x683.jpg&imgrefurl=https%3A%2F%2Fdevessport.es%2Fblog%2Fcomo-ganar-al-billar%2F&tbnid=pF71ferWSKTyUM&vet=12ahUKEwiZhKLhjKz5AhX_rpUCHei_DQYQMygAegUIARDeAQ..i&docid=9ecnCgmgSuCQrM&w=1024&h=683&q=billar&ved=2ahUKEwiZhKLhjKz5AhX_rpUCHei_DQYQMygAegUIARDeAQ);
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .contenedor-app .imagen,
.contenedor-app html {
    height: auto;
  }
}
.contenedor-app .app {
  width: 90%;
  margin: auto;
}

nav.nav {
  width: 80%;
  margin: auto;
  padding: 10px 40px;
  background-color: #594c5d;
}

.user {
  position: absolute;
  color: #d0a5c0;
  top: 0;
  left: 0;
}

.barra {
  margin: 0;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra .boton {
  background-color: #8e7c93;
  padding: 1rem 2rem;
  color: #ffffff;
  font-size: 1.8rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 0;
}
.barra .boton:hover {
  background-color: #746379;
  cursor: pointer;
}

.barra-servicios {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .barra-servicios {
    display: flex;
    justify-content: space-between;
  }
}
.barra-servicios .boton {
  margin: 0 0 0 0;
}

.tala {
  background-color: #1a2c2b;
}

.seccion {
  padding: 5rem 0;
  display: none;
}
.seccion .text-center {
  text-align: center;
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.listado-servicios {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listado-servicios .servicio {
  background-color: #ffffff;
  padding: 3rem;
  border-radius: 1rem;
  transition-property: transform;
  transition-duration: 300ms;
  text-align: center;
}
.listado-servicios .servicio.seleccionado {
  background-color: #f3740d;
}
.listado-servicios .servicio.seleccionado p {
  color: #ffffff;
}
.listado-servicios .servicio:hover {
  transform: scale(1.1);
}
.listado-servicios .servicio p {
  color: #47081d;
  margin: 0 0 1.25rem 0;
}
.listado-servicios .servicio .precio-servicio {
  color: #f3740d;
  font-weight: 700;
  font-size: 3rem;
  margin: 0;
}

.contenido-resumen p {
  margin-bottom: 1rem;
}
.contenido-resumen span {
  color: #f3740d;
  font-weight: 700;
  font-size: 2.4rem;
}

.contenedor-servicio {
  padding: 3rem 0;
  border-bottom: 1px solid #e1e1e1;
}
.contenedor-servicio:last-of-type {
  margin-bottom: 5rem;
}

.citas, .servicios {
  list-style: none;
  padding: 0;
}
.citas li, .servicios li {
  padding: 1rem;
  border-top: 1px solid #ffffff;
}
.citas li p, .servicios li p {
  font-weight: 700;
  color: #f3740d;
}
.citas li span, .servicios li span,
.citas li .servicio,
.servicios li .servicio {
  font-weight: 400;
  color: #ffffff;
}

.servicios .acciones {
  margin: 0;
}
.servicios .boton,
.servicios .boton-eliminar {
  margin: 0;
}

.formulario {
  margin-top: 2rem;
  width: 100%;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
}
.campo label {
  flex: 0 0 10rem;
  margin: auto 1rem auto 4rem;
}
.campo input {
  flex: 1;
  border: none;
  padding: 1.25rem;
  border-radius: 1rem;
}
.campo input:disabled {
  background-color: #d11755;
  color: #ffffff;
  cursor: not-allowed;
}

.boton {
  background-color: #8e7c93;
  padding: 1rem 2rem;
  color: #ffffff;
  font-size: 1.8rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  padding: 1.2rem 3rem;
  margin: auto;
}
.boton:hover {
  background-color: #746379;
  cursor: pointer;
}

.boton-eliminar {
  background-color: #cb0000;
  padding: 1rem 2rem;
  color: #ffffff;
  font-size: 1.8rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  padding: 1rem 2rem;
}
.boton-eliminar:hover {
  background-color: #980000;
  cursor: pointer;
}

.boton-crear {
  background-color: #215f05;
  padding: 1rem 2rem;
  color: #ffffff;
  font-size: 1.8rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  border-radius: 15px;
}
.boton-crear:hover {
  background-color: #102f02;
  cursor: pointer;
}

.boton-agregar {
  background-color: rgb(199, 184, 184);
  padding: 1rem 2rem;
  color: #e1e1e1;
  font-size: 1.8rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  border-radius: 15px;
}
.boton-agregar:hover {
  background-color: #b19b9b;
  cursor: pointer;
}

@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.acciones a {
  color: #ffffff;
  display: block;
  text-align: center;
}

.alerta {
  padding: 1.25rem;
  color: #ffffff;
  width: 100%;
  display: block;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}
.alerta.error {
  background-color: #cb0000;
}
.alerta.exito {
  background-color: #215f05;
}

@media (min-width: 768px) {
  .tabs {
    display: flex;
  }
}
.tabs button {
  background-color: #f3740d;
  border: none;
  color: #ffffff;
  padding: 1rem;
  display: block;
  width: 100%;
  margin-bottom: 1rem;
  text-transform: uppercase;
  font-weight: 700;
  transition: background-color 300ms ease;
}
.tabs button:hover {
  cursor: pointer;
  background-color: #c35d0a;
}
.tabs button.actual {
  background-color: #ffffff;
  color: #f3740d;
}

.paginacion {
  padding: 5rem 0;
}
@media (min-width: 480px) {
  .paginacion {
    display: flex;
    justify-content: space-between;
  }
}
.paginacion button {
  margin: 0 0 1rem 0;
}

.ocultar {
  visibility: hidden;
}

section {
  width: 90%;
}
section .cliente-contenedor {
  display: flex;
  margin: auto;
  justify-content: space-around;
  align-items: center;
}
section .cliente-contenedor p,
section .cliente-contenedor a {
  text-decoration: none;
  color: #ffffff;
  width: 100%;
  text-align: center;
}
section .cliente-contenedor a {
  width: 10%;
  margin: 1rem;
  padding: 1.2rem;
  border-radius: 3rem;
}
section .cliente-contenedor a:hover {
  color: #1e3231;
  background-color: #ffffff;
  transition: color;
  transition-delay: 4s;
}
section.mesa {
  width: 100%;
  position: relative;
  display: grid;
  gap: 55px;
  grid-template-columns: repeat(2, 1fr);
}
section.mesa .id-mesa {
  position: absolute;
}
section.mesa .mesa-contenedor {
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
  background-color: #315250;
  padding: 3px;
  border-radius: 4px;
  border: 4px solid #b2d1cf;
}
section.mesa .mesa-contenedor .mesa-listado {
  margin-bottom: 8rem;
}
section.mesa .mesa-contenedor .mesa-listado .cliente-contenedor .cliente-acciones {
  display: flex;
  width: 30%;
}
section.mesa .mesa-contenedor p.mesa {
  width: 100%;
  text-align: start;
}
section.mesa .mesa-contenedor .acciones-mesa .boton-eliminar-mesa {
  border-radius: 35px;
  padding: 14px;
  margin: auto auto auto 94%;
}
section.mesa .mesa-billar-contenedor {
  background-color: #72aaa7;
  color: #1e3231;
  border-radius: 14px;
}

.contenedor-cliente-mesa {
  display: flex;
  margin: auto;
}
.contenedor-cliente-mesa .datos-mesa {
  display: flex;
  width: 28%;
  text-align: center;
}
.contenedor-cliente-mesa .datos-mesa h4 {
  width: 100%;
  margin: auto;
  text-align: center;
}
.contenedor-cliente-mesa section.articulos {
  background-color: #315250;
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: row-reverse;
  margin: 0.3rem;
  border-radius: 20px;
  padding: 3rem;
}
.contenedor-cliente-mesa section.articulos .articulos-list {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.contenedor-cliente-mesa section.articulos .articulos-list .articulo-list {
  display: flex;
  margin: 1rem;
  align-items: center;
  justify-content: space-around;
}
.contenedor-cliente-mesa section.articulos .articulos-list .articulo-list .articulo {
  margin: auto;
  display: block;
}
.contenedor-cliente-mesa section.articulos .articulos-list .campo-articulo {
  color: #ffffff;
  text-align: center;
  border-radius: 30px;
  margin: auto;
  width: 40%;
  height: 2rem;
  background-color: #1e3231;
}
.contenedor-cliente-mesa section.articulos .cumulado {
  width: 20%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.contenedor-cliente-mesa section.articulos .cumulado p {
  padding: 1rem;
}
.contenedor-cliente-mesa section.articulos .cumulado h4 {
  padding: 1rem;
  text-align: center;
  margin: auto;
}

.modal {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.799);
  top: 0;
  right: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.modal .campo {
  width: 90%;
  margin: 10rem auto;
  display: flex;
  flex-direction: column;
}
.modal .campo h2 {
  text-align: center;
}
.modal .campo .inputs {
  text-align: center;
  margin: auto;
}
.modal .campo .inputs .submit-nueva-tarea {
  background-color: #485665;
  padding: 1rem 2rem;
  color: #ffffff;
  font-size: 1.8rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
}
.modal .campo .inputs .submit-nueva-tarea:hover {
  background-color: #333d47;
  cursor: pointer;
}
.modal .campo .inputs .cerrar-modal {
  background-color: #1e3231;
  padding: 1rem 2rem;
  color: #ffffff;
  font-size: 1.8rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
}
.modal .campo .inputs .cerrar-modal:hover {
  background-color: #0b1212;
  cursor: pointer;
}

.cerrar {
  display: none;
}