@use "base/variables" as v;
@use "base/mixins" as m;

.boton {
  @include m.boton(v.$bg-2, v.$blanco);
  padding: 1.2rem 3rem;
  margin: auto;
}
.boton-eliminar {
  @include m.boton(v.$rojo, v.$blanco);
  padding: 1rem 2rem;
}
.boton-crear {
  @include m.boton(v.$verde, v.$blanco);
  border-radius: 15px;
}
.boton-agregar {
  @include m.boton(rgb(199, 184, 184), v.$gris);
  border-radius: 15px;
}
